import React from 'react'
import styled, { keyframes } from 'styled-components'

const load2 = keyframes`
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
`

const SpinnerDiv = styled.div`
  &,
  &:after {
    border-radius: 50%;
    width: 10em;
    height: 10em;
  }

  margin: 60px auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(128, 64, 64, 0.2);
  border-right: 1.1em solid rgba(128, 64, 64, 0.2);
  border-bottom: 1.1em solid rgba(128, 64, 64, 0.2);
  border-left: 1.1em solid #804040;
  transform: translateZ(0);
  animation: ${load2} 1.1s infinite linear;
`

const Spinner = () => <SpinnerDiv>Loading...</SpinnerDiv>

export default Spinner



